@font-face {
    font-family: HiguenSerif;
    src: url( "/ikigai/assets/fonts/Higuen Serif.otf" ) format("opentype");
}

@font-face {
    font-family: Arapey;
    src: url("/ikigai/assets/fonts/Arapey/Arapey-Regular.ttf") format("truetype");
}

@font-face {
    font-family: Poppins;
    src: url("/ikigai/assets/fonts/Poppins/Poppins-Light.ttf") format("truetype");
}

@font-face {
    font-family: Poppins;
    font-weight: bold;
    src: url("/ikigai/assets/fonts/Poppins/Poppins-Medium.ttf") format("truetype");
}


* {
	box-sizing: border-box;
}
body {
	margin: 0;
	color: #2d3142;
	font-family: Poppins;
	font-size: .9em;
}

h1,h2,h3,h4,h5,h6 {
	font-family: Arapey;
}

.fixed-background {
	background-image: url(/ikigai/assets/images/BACKGROUND.png);
	background-position: right;
	height: 100vh;
	width: 100vw;
	position: fixed;
	z-index: -1;
	top: 0;
	left: 0;
}

.page-main {
	min-height: 100vh;
	padding: 2em;
	display: flex;
	align-items: center;
	width: 100%
}

.main-content {
	min-width: 75%;
	max-width: 800px;
    margin: auto;
    padding: 2em;
    background: rgba(255,253,247,.33);
    border-radius: 2em;
}

.zine-name {
	text-align: center;
	margin-top: 0;
}
.title {
    color: #fffdf7;
    font-family: HiguenSerif;
    font-size: 4em;
}

.subtitle {
	font-family: Arapey;
	color: #6798c0;

}